.LoginContainer{
    margin-top: 15px;
display: flex;
align-items: center;
justify-content: center;
flex-direction: column;
position: relative;
background: linear-gradient(135deg, rgba(36, 46, 77, 0.9), rgba(137, 126, 121, 0.9));
font-family: "Roboto", helvetica, arial, sans-serif;
font-size: 1.5em;
}

.login-form {
width: 100%;
padding: 2em;
position: relative;
background: rgba(0, 0, 0, 0.15);
margin-top: 20px;
}
.login-form:before {
content: "";
position: absolute;
top: -2px;
left: 0;
height: 2px;
width: 100%;
background: linear-gradient(to right, #35c3c1, #00d6b7);
}
@media screen and (min-width: 600px) {
.login-form {
  width: 76vw;
  max-width: 32em;
}
}

.flex-row {
display: flex;
margin-bottom: 1em;
}

.lf--label {
width: 2em;
display: flex;
align-items: center;
justify-content: center;
background: #f5f6f8;
cursor: pointer;
}

.lf--input {
flex: 1;
padding: 1em;
border: 0;
color: #8f8f8f;
font-size: 1rem;
}
.lf--input:focus {
outline: none;
transition: transform 0.15s ease;
transform: scale(1.1);
}

.lf--submit {
 margin: 7px 0 6px;
display: block;
padding: 1em;
width: 100%;
background: linear-gradient(to right, #35c3c1, #00d6b7);
border: 0;
color: #fff;
cursor: pointer;
font-size: 0.75em;
font-weight: 600;
text-shadow: 0 1px 0 rgba(0, 0, 0, 0.2);
text-decoration: none;
text-align: center;
}
.lf--submit:hover{
    color: #B38533;
    background: #ffff;

  }
.lf--submit:focus {
outline: none;
transition: transform 0.15s ease;
transform: scale(1.1);
}

.lf--forgot {
margin-top: 1em;
color: #00d6b7;
font-size: 0.65em;
text-align: center;
position: relative;
}

::-moz-placeholder {
color: #8f8f8f;
}

:-ms-input-placeholder {
color: #8f8f8f;
}

::placeholder {
color: #8f8f8f;
}
@media screen and (min-width: 277px) {
  .login-form {
    padding: 1em;
}
.lf--input {
font-size: 0.9rem;
}
}